import { NextRequest, NextResponse } from "next/server"
import { activityLogs, pickupRequests, wasteLogs, users } from "@/app/api/_data"
import type { PickupRequest } from "@/app/api/_types"

type CreateRequestBody = {
  // New format
  charityId?: string
  itemId?: string
  cafeId?: string
  status?: string
  note?: string
  // Legacy format (for backward compatibility)
  wasteId?: string
  requesterId?: string
  preferredTime?: string
  notes?: string
}

// GET /api/charity/requests
// Returns pickup requests created by the charity user (sorted by newest first).
// Public endpoint - uses x-user-id header
export async function GET(req: NextRequest) {
  // Get user ID from header (public access)
  const userId = req.headers.get("x-user-id")
  if (!userId) {
    return NextResponse.json({ error: "Missing user ID" }, { status: 400 })
  }
  
  const { users } = await import("@/app/api/_data")
  const user = users.find((u) => u.id === userId && u.role === "charity")
  if (!user) {
    return NextResponse.json({ error: "Invalid charity user" }, { status: 400 })
  }

  const items = pickupRequests
    .filter((p) => p.requesterId === user.id && p.requesterType === "charity")
    .sort((a, b) => b.requestedAt.localeCompare(a.requestedAt))
    .map((p) => {
      // Include waste details
      const waste = wasteLogs.find((w) => w.id === p.wasteId)
      const cafeUser = users.find((u) => u.cafeId === waste?.cafeId)
      
      // Parse pickupWindow from preferredTime if status is scheduled
      // Format: "YYYY-MM-DD HH:MM-HH:MM" or similar
      let pickupWindow = undefined
      if (p.status === "scheduled" && p.preferredTime) {
        // Try to parse preferredTime - it might be in format "YYYY-MM-DD HH:MM" or "YYYY-MM-DD HH:MM-HH:MM"
        const timeMatch = p.preferredTime.match(/^(\d{4}-\d{2}-\d{2})\s+(\d{2}:\d{2})(?:-(\d{2}:\d{2}))?$/)
        if (timeMatch) {
          pickupWindow = {
            date: timeMatch[1],
            from: timeMatch[2],
            to: timeMatch[3] || timeMatch[2], // If no end time, use start time
          }
        } else {
          // Fallback: try to extract date and time
          const parts = p.preferredTime.split(" ")
          if (parts.length >= 2) {
            pickupWindow = {
              date: parts[0],
              from: parts[1],
              to: parts[1], // Default to same time if no end time
            }
          }
        }
      }
      
      // Get café contact information (admin or staff user)
      const cafeContact = users.find((u) => u.cafeId === waste?.cafeId && (u.role === "admin" || u.role === "staff"))
      
      return {
        ...p,
        itemName: waste?.itemName,
        quantity: waste?.quantity,
        cafeName: cafeUser?.organizationName || "Green Café",
        cafeId: waste?.cafeId,
        pickupWindow,
        // Include café contact information
        cafeContact: cafeContact ? {
          name: cafeContact.name,
          email: cafeContact.email,
          organizationName: cafeContact.organizationName,
        } : null,
      }
    })

  return NextResponse.json(items)
}

// POST /api/charity/requests
// Creates a pickup request for an edible, pending waste item.
// Allows public requests - validates requesterId/charityId exists
export async function POST(req: NextRequest) {
  const body = (await req.json()) as Partial<CreateRequestBody>
  
  // Support both new format (charityId, itemId) and legacy format (requesterId, wasteId)
  const charityId = body.charityId || body.requesterId
  const itemId = body.itemId || body.wasteId
  
  if (!itemId || !charityId) {
    return NextResponse.json({ error: "Missing itemId/wasteId or charityId/requesterId" }, { status: 400 })
  }

  // Validate requester exists and is a charity user
  const { users } = await import("@/app/api/_data")
  const requester = users.find((u) => u.id === charityId)
  if (!requester) {
    return NextResponse.json({ error: "Invalid charityId/requesterId" }, { status: 400 })
  }
  if (requester.role !== "charity") {
    return NextResponse.json({ error: "Requester must be a charity user" }, { status: 403 })
  }

  const waste = wasteLogs.find((w) => w.id === itemId)
  if (!waste) {
    return NextResponse.json({ error: "Waste item not found" }, { status: 404 })
  }
  
  // Validate cafeId if provided (new format)
  if (body.cafeId && waste.cafeId !== body.cafeId) {
    return NextResponse.json({ error: "Cafe ID mismatch" }, { status: 400 })
  }

  // Validate waste is available for charity
  if (!(waste.type === "edible" && waste.status === "pending" && waste.assignedTo === "charity")) {
    return NextResponse.json({ error: "Waste item is not available for charity" }, { status: 400 })
  }

  // Check if this waste already has a pickup request
  const existingRequest = pickupRequests.find((p) => p.wasteId === waste.id)
  if (existingRequest) {
    return NextResponse.json({ error: "This waste item already has a pickup request" }, { status: 400 })
  }

  const now = new Date().toISOString()
  const newRequest: PickupRequest = {
    id: `p-${Date.now()}`,
    wasteId: waste.id,
    requesterId: requester.id,
    requesterType: "charity",
    requestedAt: now,
    status: (body.status as any) || "pending",
    preferredTime: body.preferredTime,
    notes: body.note || body.notes,
  }

  pickupRequests.push(newRequest)

  // Add activity log with pickupRequested action
  activityLogs.push({
    id: `a-${Date.now()}`,
    userId: requester.id,
    actionType: "pickupRequested",
    itemName: waste.itemName,
    requesterType: "charity",
    createdAt: now,
  })

  return NextResponse.json(newRequest, { status: 201 })
}

